//  -> openDocument loading

export function openDocumentFromUrl(file) {
  const url = typeof file === 'string' ? file : file.url;
  const fileType = typeof file === 'object' && file.type ? file.type : '';

  if (!url) {
    uni.showToast({ title: '', icon: 'none' });
    return;
  }

  // 
  const unsupported = ['rar', '7z', 'zip', 'tar', 'gz', 'bz2', 'exe', 'dmg', 'iso', 'bin'];
  const ext = (fileType || url.split('?')[0].split('.').pop() || '').toLowerCase();
  
  // 
  const supported = ['pdf', 'doc', 'docx', 'xls', 'xlsx', 'ppt', 'pptx', 'txt', 'rtf'];
  const imageTypes = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp'];
  
  if (unsupported.includes(ext)) {
    uni.showModal({
      title: '',
      content: '',
      showCancel: false
    });
    return;
  }
  
  // uni.previewImage
  if (imageTypes.includes(ext)) {
    uni.previewImage({
      urls: [url],
      current: url,
      success: () => {
        console.log('');
        uni.hideLoading();
      },
      fail: (err) => {
        console.error(':', err);
        uni.hideLoading();
        uni.showToast({ title: '', icon: 'none' });
      }
    });
    return;
  }
  
  const doDownload = (retry = false) => {
    const timer = setTimeout(() => {
      //  fail 
      uni.hideLoading();
      if (!retry) {
        console.log('...');
        doDownload(true);
      } else {
        console.log('');
        uni.showToast({ title: '', icon: 'none' });
      }
    }, 20000); // 20

    // 
    if (!supported.includes(ext)) {
      uni.showModal({
        title: '',
        content: ` ${ext.toUpperCase()} `,
        success: (res) => {
          if (res.confirm) {
            // hideLoading
          } else {
            uni.hideLoading();
            return;
          }
        }
      });
    }

    console.log(':', url);
    uni.downloadFile({
      url,
      success: (res) => {
        clearTimeout(timer);
        console.log(':', res);
        if (res.statusCode === 200) {
          console.log(':', res.tempFilePath, ':', ext);
          uni.openDocument({
            filePath: res.tempFilePath,
            fileType: ext,
            showMenu: true,
            success: () => {
              console.log('');
              uni.hideLoading();
            },
            fail: (err) => {
              uni.hideLoading();
              console.error(':', err);
              if (!retry) {
                console.log('...');
                doDownload(true);
              } else {
                uni.showToast({ title: '', icon: 'none' });
              }
            }
          });
        } else {
          uni.hideLoading();
          console.error(':', res.statusCode);
          if (!retry) {
            console.log('...');
            doDownload(true);
          } else {
            uni.showToast({ title: `(:${res.statusCode})`, icon: 'none' });
          }
        }
      },
      fail: (err) => {
        clearTimeout(timer);
        uni.hideLoading();
        console.error(':', err);
        
        // OSS
        if (!retry && url.includes('llzm-lixiaobao.oss-cn-beijing.aliyuncs.com')) {
          console.log('OSS...');
          const proxyUrl = `https://llzmlxb.xyz/api/proxy-download?url=${encodeURIComponent(url)}`;
          console.log('URL:', proxyUrl);
          
          uni.downloadFile({
            url: proxyUrl,
            success: (res) => {
              if (res.statusCode === 200) {
                uni.openDocument({
                  filePath: res.tempFilePath,
                  fileType: ext,
                  showMenu: true,
                  success: () => {
                    console.log('');
                    uni.hideLoading();
                  },
                  fail: (err) => {
                    uni.hideLoading();
                    console.error(':', err);
                    uni.showToast({ title: '', icon: 'none' });
                  }
                });
              } else {
                uni.showToast({ title: '', icon: 'none' });
              }
            },
            fail: (err) => {
              console.error(':', err);
              uni.showToast({ title: '', icon: 'none' });
            }
          });
          return;
        }
        
        if (!retry) {
          console.log('...');
          doDownload(true);
        } else {
          uni.showToast({ title: '', icon: 'none' });
        }
      }
    });
  };

  uni.showLoading({ title: '...', mask: true });
  doDownload(false);
}

export default { openDocumentFromUrl };



