/**
 * 
 * 
 */
export default {
  data() {
    return {
      careMode: false // 
    }
  },

  // 
  onLoad() {
    this.loadCareMode();
  },
  onShow() {
    this.loadCareMode();
  },

  methods: {
    // 
    loadCareMode() {
      const savedCareMode = uni.getStorageSync('careMode');
      // null
      if (savedCareMode !== null) {
        this.careMode = savedCareMode;
        this.applyCareMode(); // 
      }
    },

    // 
    applyCareMode() {
      console.log(':', this.careMode ? '' : '');
    },

    // 
    toggleCareMode() {
      this.careMode = !this.careMode;
      uni.setStorageSync('careMode', this.careMode); // 
      this.applyCareMode();
      // 
      uni.showToast({
        title: this.careMode ? '' : '',
        icon: 'success',
        duration: 1500
      });
    }
  }
}

